.HeaderContainer {
    height: 15vh;

    background-color: black;
    color: white;

    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    padding: 0px 35px;
}

.Logo {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Lines {
    stroke: white;
}

.LogoText {
    position: absolute;
}

.LogoMatsu {
    font-size: 1.33rem;
    transition: font-size 1s;
    color: black;
    text-shadow: 
    0 0 5px #E8E6EA,
    0 0 10px #E8E6EA,
    0 0 15px #E8E6EA,
    0 0 20px #E8E6EA,
    0 0 25px #E8E6EA,
    0 0 42px #E8E6EA,
    0 0 82px #E8E6EA,
    0 0 92px #E8E6EA,
    0 0 102px #E8E6EA,
    0 0 151px #E8E6EA;
}

.LogoMatsuOn {
    /* animation: flicker 1.5s infinite alternate; */
    font-size: 3rem;
    transition: font-size 1s;
    color: black;
    text-shadow: 
    0 0 5px #E8E6EA,
    0 0 10px #E8E6EA,
    0 0 15px #E8E6EA,
    0 0 20px #E8E6EA,
    0 0 25px #E8E6EA,
    0 0 42px #E8E6EA,
    0 0 82px #E8E6EA,
    0 0 92px #E8E6EA,
    0 0 102px #E8E6EA,
    0 0 151px #E8E6EA;
}

.LogoMoto {
    font-size: 1.33rem;
    transition: font-size 1s;
    color: black;
    text-shadow: 
        0 0 5px #E8E6EA,
        0 0 10px #E8E6EA,
        0 0 15px #E8E6EA,
        0 0 20px #E8E6EA,
        0 0 25px #E8E6EA,
        0 0 42px #E8E6EA,
        0 0 82px #E8E6EA,
        0 0 92px #E8E6EA,
        0 0 102px #E8E6EA,
        0 0 151px #E8E6EA;
}

.LogoMotoOn {
    font-size: 3rem;
    transition: font-size 1s;
    color: black;
    text-shadow: 
        0 0 5px #E8E6EA,
        0 0 10px #E8E6EA,
        0 0 15px #E8E6EA,
        0 0 20px #E8E6EA,
        0 0 25px #E8E6EA,
        0 0 42px #E8E6EA,
        0 0 82px #E8E6EA,
        0 0 92px #E8E6EA,
