}

.HeaderLinks {

}

.HeaderLinks ul {
    list-style-type: none;
    font-size: 1.33rem;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    margin: 0;
}

.HeaderLinks li {
    height: 15vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin: 0px 25px;
}

.HeaderLinks a {
    color: white;
    text-decoration: none;
}
.HeaderLinks a:hover {
    color: gray;
}
