.TeacherBookReportsContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.TeacherBookReportsHeader {
    margin: 25px 0px;
}

.TeacherBookReportsButtons {
    display: grid;

    grid-template-columns: repeat(2, 0fr);  
    row-gap: 20px;
    column-gap: 20px;
    /* grid-template-columns: repeat(2, 0.5fr);  
    grid-gap: 10px 5px;  */

    padding: 0px 50px;
}

.TeacherBookReportsData {
    min-height: 250px;
    width: 65vw;
    border: 2px solid black;

    padding: 20px;
    margin: 50px;
}