.BlogContainer {
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.BlogDate {
    width: 100%;
    font-family: 'Primitive';
    font-size: 1rem;

    text-align: left;
}

.BlogEntry {
    width: 100%;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 1rem;

    text-align: left;
}

.BlogEntry li {
    list-style-type: circle;
}