.BookReportContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 0 10vw;
}

.BookReportTitle {
    font-size: 3rem;
    font-family: 'Primitive';

    margin: 33px 0px;
}

.BookReportSelectionBar {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    
    margin: 5px 0px 35px 0px;
}

.BookDropdown {
    margin: 0px 15px;
}

.ChapterDropdown {
    margin: 0px 15px;
}

.SelectionDisplay {
    width: 75%;
    margin: 15px 0px 0px 0px;
}

.Selection {
    font-size: 1.33rem;
    text-align: left;
}

.ReportInput {
    width: 75%;
    /* height: 300px;
    border: 2px solid black;
    margin: 25px 0px; */
}

.ReportTextArea {
    max-height:'75px';
    min-height:'38px';
    width: 100%;
    height: 300px;
    resize:'none';
    padding:'9px';
    box-sizing:'border-box';
    font-size:'15px';

    margin: 20px 0px;
}

.ReportControls {
    display: flex;
    flex-direction: row;
    justify-content: right;
    align-items: center;

    margin: 0 0 20px 0;
}

